import ShopList from '../../components/shopList/shopList.vue'
import {request} from '../../utils/request.js'
export default {
	components: {ShopList},
	data() {
		return {
			navIndex: 0,
			sortType: true, // true代表升序， false代表降序
			clickCount: 0,
			shopListArr: [],
			newShopList: []
		}
	},
	methods: {
		handleClick(index){
			this.newShopList = []
			this.navIndex = index
			uni.showToast({
				icon: 'loading',
				title: '正在加载'
			})
			setTimeout(() => {
				// 修改导航标识
				if(index === 2){
					// 判断点击的是否是价格排序
					this.clickCount++
					// 通过点击的次数验证排序的方式，奇数为升序，偶数为降序
					if(this.clickCount%2 !== 0){ //  升序
						this.sortType = true
						this.newShopList = [...this.shopListArr].sort((a, b) => a.retailPrice - b.retailPrice)
					}else {  // 降序
						this.sortType = false
						this.newShopList = [...this.shopListArr].sort((a, b) => b.retailPrice - a.retailPrice)
					}
					
				}else {
					// 当点击其他的导航选项应该清空计数器
					this.clickCount = 0
					this.newShopList = this.shopListArr.reverse()
				}
				uni.hideToast()
			}, 1500)
		}
	},
	async mounted(){
		uni.showToast({
			icon: 'loading',
			title: '正在加载'
		})
		let result = await request('/getCateList')
		this.shopListArr = result[0].itemList
		this.newShopList = [...this.shopListArr]
		uni.hideToast()
	}
}
